.item {
    position: fixed;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
}

.item img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    min-width: 50%;
    min-height: 50%;
}

h1 {
    color: rgb(255, 255, 255);
    z-index: 3;
    position: relative;
    text-align: center;
    padding-bottom: 50px;
    padding-top: 50px;
}